import { Tab, Tabs } from '@antmjs/vantui'
import { Component } from 'react'
import DemoPage from '../../components/demo-page/index'
import DemoBlock from '../../components/demo-block/index'
import Demo1 from './demo1'
import Demo2 from './demo2'
import Demo3 from './demo3'

export default class Index extends Component {
  constructor() {
    super()
  }
  state = { active: 0 }
  render() {
    return (
      <DemoPage title="滚动&下拉刷新" className="pages-power-scroll-view-index">
        <Tabs
          active={this.state.avtive}
          onChange={(e) => this.setState({ active: e.detail.index })}
          sticky={true}
        >
          <Tab title="基础用法" />

          <Tab title="自定义参数" />

          <Tab title="配合搜索使用" />
        </Tabs>

        {this.state.active === 0 ? <Demo1 /> : ''}

        {this.state.active === 1 ? <Demo2 /> : ''}

        {this.state.active === 2 ? <Demo3 /> : ''}
      </DemoPage>
    )
  }
}
