import { createSelectorQuery } from '@tarojs/taro'
function selectorQuery(nodesRef) {
  if (!nodesRef || nodesRef.nodeName === 'root') {
    return createSelectorQuery().selectViewport()
  } else {
    return createSelectorQuery().select('#' + nodesRef.uid)
  }
}
export function scrollOffset(nodesRef) {
  return new Promise((resolve) => {
    // 没有固定高度-- 就去拿page的高度
    if (process.env.TARO_ENV === 'h5') {
      const _nodesRef = nodesRef || document.documentElement || document.body
      return resolve({
        scrollLeft: _nodesRef.scrollLeft,
        scrollTop: _nodesRef.scrollTop,
      })
    }
    return selectorQuery(nodesRef)
      .scrollOffset()
      .exec((res) => {
        resolve(res[0] || res)
      })
  })
}
export function boundingClientRect(nodesRef) {
  return new Promise((resolve) => {
    if (process.env.TARO_ENV === 'h5') {
      const _nodesRef = nodesRef || document.documentElement || document.body
      return resolve(_nodesRef.getBoundingClientRect())
    }
    return selectorQuery(nodesRef)
      .boundingClientRect()
      .exec((res) => {
        resolve(res[0])
      })
  })
}
export const stopPropagation = (event) => event.stopPropagation()
export function preventDefault(event, isStopPropagation) {
  /* istanbul ignore else */ // if (typeof event?.cancelable !== 'boolean' || event?.cancelable) {
  // }
  event.preventDefault()
  if (isStopPropagation) {
    stopPropagation(event)
  }
}
export const debounce = (fn, timeout) => {
  let handle = 0
  let lastArgs = null
  const ret = (...args) => {
    lastArgs = args
    clearTimeout(handle)
    handle = setTimeout(() => {
      lastArgs = null
      fn(...args)
    }, timeout)
  }
  ret.flush = () => {
    clearTimeout(handle)
    if (lastArgs) {
      const _lastArgs = lastArgs
      lastArgs = null
      return fn(..._lastArgs)
    }
  }
  // ret.cancel = () => {
  //   lastArgs = null
  //   clearTimeout(handle)
  // }
  return ret
}
